emes\";\n\n:root {\n    --rotation-1: -60deg;\n    --rotation-2: 0deg;\n    --rotation-3: 60deg;\n    --rotation-4: 120deg;\n    --rotation-5: 180deg;\n    --rotation-6: 240deg;\n}\n\n.full-row {\n    --radius: 216px;\n}\n\n.small {\n    --width: 100vw;\n    --radius: calc(0.42 * var(--width));\n}\n\n.x-small {\n    --width: 100vw;\n    --radius: calc(0.40 * var(--width));\n}\n\n@include themify($themes) {\n    #projects {\n        text-decoration-color: themed(primary);\n    }\n\n    .grid > div {\n        background-color: themed(opacity);\n        border: 2px solid themed(text);\n    }\n\n    .featured-p {\n        background-color: themed(secondary);\n        color: themed(contrast);\n    }\n\n    .project-text {\n        border: 2px solid themed(text);\n\n        a {\n            color: themed(tertiary);\n\n            &:hover {\n                text-decoration: underline;\n            }\n        }\n    }\n}\n\n#projects {\n    text-decoration: underline;\n    text-underline-offset: 8px;\n}\n  \n.grid {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));\n    gap: 16px;\n    width: 100%;\n\n    > div {\n        display: flex;\n        color: white;\n        border-radius: 10px;\n        min-height: 300px;\n        overflow: auto;\n    }\n}\n\n.featured-div {\n    position: relative;\n    overflow: visible !important;\n    border-radius: 10px 0px 10px 10px !important;\n}\n\n.featured-p {\n    position: absolute;\n    top: -32px;\n    right: -2px;\n    padding: 4px 8px 4px;\n    margin: 0;\n    border-radius: 10px 10px 0px 0px;\n}\n\n.full-row {\n    grid-column: 1 / -1;\n}\n\n.project-img {\n    width: 550px;\n    padding: 32px;\n    overflow: hidden;\n\n    img {\n        width: 100%;\n        border-radius: 10px;\n        object-fit: contain;\n    }\n}\n\n.carousel-container {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    align-items: center;\n    padding: 32px;\n    width: 550px;\n}\n\n.img-container {\n    display: flex;\n    margin-top: 10px;\n    width: 504px;\n    height: 156px;\n    perspective: 1000px;\n}\n\n.carousel {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    height: 100%;\n    width: 100%;\n    transform-style: preserve-3d;\n    transition: transform 1s;\n}\n\n.item {\n    display: block;\n    position: absolute;\n    aspect-ratio: 16 / 9;\n    width: 240px;\n    height: auto;\n    text-align: center;\n    color: #FFF;\n    border-radius: 10px;\n    overflow: hidden;\n\n    img {\n        width: 100%;\n        height: 100%;\n        object-fit: contain;\n    }\n}\n\n.a {\n    transform: rotateY(var(--rotation-1)) translateZ(var(--radius));\n}\n.b {\n    transform: rotateY(var(--rotation-2)) translateZ(var(--radius));\n}\n.c {\n    transform: rotateY(var(--rotation-3)) translateZ(var(--radius));\n}\n.d {\n    transform: rotateY(var(--rotation-4)) translateZ(var(--radius));\n}\n.e {\n    transform: rotateY(var(--rotation-5)) translateZ(var(--radius));\n} \n.f {\n    transform: rotateY(var(--rotation-6)) translateZ(var(--radius));\n}\n\n.project-info {\n    padding: 32px;\n    box-sizing: border-box;\n    line-height: 1.5;\n    width: 50%;\n    min-width: 300px;\n}\n\n.title-1 {\n    display: none;\n}\n\nh2 {\n    text-align: center;\n    margin: 0;\n    font-size: 2rem;\n}\n\n.project-text {\n    padding: 8px;\n    border-radius: 10px;\n    height: 120px;\n    overflow: auto;\n}\n\nkbd {\n    font-family: monospace;\n    padding: 2px 4px;\n    border: 1px solid black;\n    border-radius: 3px;\n    box-shadow: inset 0 -2px 0 black;\n}\n\ncode {\n    font-family: monospace;\n    padding: 2px 4px;\n    border-radius: 3px;\n    word-break: break-all;\n}\n\n.dark-theme kbd {\n    background-color: rgb(227, 230, 232);\n    color: black;\n}\n\n.light-theme kbd {\n    background-color: rgb(59, 59, 59);\n    color: white;\n}\n\n.dark-theme code {\n    background-color: rgb(227, 230, 232);\n    color: black;\n}\n\n.light-theme code {\n    background-color: rgb(59, 59, 59);\n    color: white;\n}\n\n.project-btn-container {\n    display: flex;\n    align-items: center;\n    margin-top: 24px;\n    gap: 8px;\n\n    .front {\n        padding-left: 0;\n        padding-right: 0;\n    }\n\n    a {\n        padding: 6px 15px;\n    }\n}\n\n.pushable {\n    border-radius: 12px;\n    border: none;\n    padding: 0;\n    cursor: pointer;\n    outline-offset: 4px;\n}\n\n.front {\n    display: block;\n    background: white;\n    padding: 6px 15px;\n    border-radius: 12px;\n    font-size: 1rem;\n    transform: translateY(-6px);\n}\n\n.pushable:active .front {\n    transform: translateY(-2px);\n}\n\n.dark-theme .pushable {\n    background: black;\n}\n\n.light-theme .pushable {\n    background: rgb(59, 59, 59);\n}\n\n.dark-theme .front {\n    background: rgb(139, 233, 253);\n}\n\n.light-theme .front {\n    background: white;\n}\n\n@mixin medium-project-div {\n    flex-direction: column;\n    align-items: center;\n    padding: 16px;\n\n    .carousel-container, .project-info {\n        padding: 0;\n    }\n\n    .carousel-btns {\n        margin-top: 24px;\n    }\n\n    .title-1 {\n        display: block;\n        margin-bottom: 16px;\n    }\n\n    .title-2 {\n        display: none;\n    }\n\n    .project-info {\n        display: flex;\n        flex-direction: column;\n        justify-content: center;\n        align-items: center;\n    }\n\n    .project-text {\n        margin-top: 16px;\n    }\n}\n\n@mixin small-project-div {\n    @include medium-project-div;\n\n    .project-img {\n        padding: 8px;\n        width: 100%;\n    }\n\n    .carousel-container {\n        width: 100%;\n    }\n\n    .img-container {\n        width: 100%;\n    }\n\n    .item {\n        width: 50%;\n    }\n}\n\n@mixin x-small-project-div {\n    @include small-project-div;\n\n    .title-1 {\n        margin-bottom: 0;\n    }\n\n    .carousel-btns {\n        margin-top: 0;\n    }\n\n    .project-info {\n        width: 100%;\n        min-width: min-content;\n    }\n}\n\n.small {\n    @include small-project-div;\n}\n\n.x-small {\n    @include x-small-project-div;\n}\n\n@media (max-width: 1172px) {\n    .full-row {\n        @include medium-project-div;\n    }\n}\n\n@media (max-width: 735px) {\n    .full-row {\n        --radius: 30vw;\n        @include small-project-div;\n    }\n}\n\n@media (max-width: 584px) {\n    .full-row {\n        --radius: 28vw;\n        @include x-small-project-div;\n    }\n}"],"sourceRoot":""}]);
